  @font-face {
    font-family: "gontserrat-bold";
    src: url('../../fonts/Gontserrat-ExtraBold.ttf');
        
  }.amenity-slider {
    width: 100%; /* Full width slider */
    margin: 0 auto;
    box-sizing: border-box;
  }
  
  .amenity-slider .slick-slide {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 0; /* Remove padding */
    margin: 0; /* Remove margin */
  }
  
  .amenity-slider .slick-slide-content {
    padding: 0; /* Remove padding */
    margin: 0; /* Remove margin */
  }
  .slick-slide{
    overflow: hidden !important;
    width: max-content !important;
  }
  .slick-track{
    width: max-content !important;

  }
  .amenity-slider img {
    width: 100%;
    display: block;
    filter: grayscale(100%); 
    transition: filter 0.3s ease;

  }
  .mobile-view-slid img {
    width: 100%;
    height: 100%; /* Set a fixed height for all images */
    display: block;
    filter: grayscale(100%); 
    transition: filter 0.3s ease;

  }
  .amenity-slider img:hover{
    box-shadow: 0 0 20px rgba(0, 0, 0, 0.886); 
    filter: none; 
  }
  .mobile-view-slid img:hover{
    box-shadow: 0 0 20px rgba(0, 0, 0, 0.886); 
    filter: none; 
  }
 
  

  
 
 
  
  
 
    .caption {
       
    visibility: hidden;
    position: absolute;
    right: 0;
    bottom: -4px;
    height: -webkit-fill-available;    width:60%;
    background-color: #f07c00cf;
    text-align: end;
    clip-path: polygon(40% 0, 100% 0, 100% 100%, 0 100%);

    }
    .caption-image-container img{
        width: 47px;
        height: 35px;
        position: absolute;
        margin-top: 10%;
        right: 8%;
       

    }
    .caption-description-container{
       margin-top: 60%;
       margin-right: 8%;
       margin-left: 15%;
       text-align: end;
    }
    .caption-image-container{
        position: relative;
        
        
    }
    .slide--has-caption:hover .caption {
    visibility: visible!important;
    opacity: 1;
    cursor: pointer;
    pointer-events: none;
    z-index: 3;}
